import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Neighborhood(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/neighborhood.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[6.6, -1.41, 2.21]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere164.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere164_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere164_2.geometry}
          material={materials.outline}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere164_3.geometry}
          material={materials["glass.005"]}
        />
      </group>
    </group>
  );
}
export default Neighborhood;
useGLTF.preload("/glossarySelected/neighborhood.glb");
